import React from 'react'
import './Purpose.css';
function Purpose() {
      //     .
  const backgroundImage = window.innerWidth > 751 ? 
  `url(${process.env.PUBLIC_URL}/images/about/brand_belief_visual_img_pc_kr_230511.jpg)` : 
  `url(${process.env.PUBLIC_URL}/images/about/brand_belief_visual_img_mo_kr_230511.jpg)`;

  return (
    <section className="purpose" style={{ backgroundImage }}>
        <p className="title">
            <span>BUILDING A WORLD OF BEAUTY</span>
            <br/>
            POWERED BY ART AND HERITAGE
        </p>
        <p className="subtitle">
            OUR PURPOSE
        </p>
        <p className="mtext">
                .
        </p>
        <p className="stext">
                  
            <br/>
                    .
        </p>
        <p className="stext">
                  
            <br/>
                   .
        </p>
    </section>
  )
}

export default Purpose