import React from 'react'
import './SectionBelief.css';

function SectionBelief() {

  return (
    <section className="belief" id="belief">
        <div className="section_inner">
            <div className="section_body">
                <div className="section_text" data-aos="fade-up" data-aos-duration="1500" data-aos-anchor-placement="center-bottom">
                    <h1>our belief</h1>
                    <h2>
                             .
                        <br/>
                        <span className="mo-margin"></span>
                            
                        <br className="mo-only"/>
                            .
                        <br/>
                        <span className="mo-margin"></span>
                              
                        <br className="mo-only"/>
                                 
                        <br className="mo-only"/>
                          .
                        <br/>
                        <span className="mo-margin"></span>
                              
                        <br className="mo-only"/>
                             .
                    </h2>
                </div>
                <div className="section_img" data-aos="fade-up" data-aos-duration="1500" data-aos-anchor-placement="center-bottom">
                    <picture>
                        <source media="(max-width: 750px)" srcSet={`${process.env.PUBLIC_URL}/images/about/brand_belief_content_img01_mo_kr_230511.jpg`}/>
                        <source media="(min-width: 1023px)" srcSet={`${process.env.PUBLIC_URL}/images/about/brand_belief_content_img01_pc_kr_230511.jpg`}/>
                        <img src={`${process.env.PUBLIC_URL}/images/about/brand_belief_content_img01_pc_kr_230511.jpg`} alt=" "/>
                    </picture>
                </div>
            </div>
        </div>
    </section>
  )
}

export default SectionBelief